  src: url("../fonts/roboto-bold-webfont.woff2") format("woff2"), url("../fonts/roboto-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@keyframes topblink {
  0% {
    fill: #4ba9ff;
  }
  33% {
    fill: rgba(255, 255, 255, 0.4);
  }
  66% {
    fill: rgba(255, 255, 255, 0.8);
  }
  100% {
    fill: #4ba9ff;
  }
}

@keyframes middleblink {
  0% {
    fill: rgba(255, 255, 255, 0.8);
  }
  33% {
    fill: #4ba9ff;
  }
  66% {
    fill: rgba(255, 255, 255, 0.4);
  }
  100% {
    fill: rgba(255, 255, 255, 0.8);
  }
}

@keyframes bottomblink {
  0% {
    fill: rgba(255, 255, 255, 0.4);
  }
  33% {
    fill: rgba(255, 255, 255, 0.8);
  }
  66% {
    fill: #4ba9ff;
  }
  100% {
    fill: rgba(255, 255, 255, 0.4);
  }
}

.icon-arrow {
  width: 2em;
  height: 1em;
  fill: #010101;
}

.icon-heart {
  width: 1.12em;
  height: 1em;
  fill: #231f20;
}

.icon-twitter {
  width: 1em;
  height: 1em;
  fill: initial;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

